.checkbox {
  position: relative;
}

.checkbox__input {
  appearance: none;
  position: absolute;
}

.checkbox__icon {
  position: absolute;
  top: -13px;
  left: 15px;
  width: 46px;
  height: 46px;
  background-color: @grey-form-catalog;
  border-radius: 50%;
}

.checkbox__label {
  padding-left: 62px;
}

.checkbox__input:checked + .checkbox__icon {
  background-image: url("../img/pin.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.checkbox__input:hover + .checkbox__icon,
.checkbox__input:checked:hover + .checkbox__icon {
  background-color: @grey-disable;
}

.checkbox__input:active + .checkbox__icon,
.checkbox__input:checked:active + .checkbox__icon {
  background-image: url("../img/pin-active.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-color: @grey-active;
}
